@import url("https://fonts.googleapis.com/css2?family=Oswald:wght@500&family=Roboto:wght@100;300;400;500;700;900&display=swap");
body {
  background-color: rgb(59, 49, 49);
  font-family: "Roboto", sans-serif;
  margin: 0%;
}
.movieContainer {
  /*flex-wrap: wrap;*/
  border-color: coral;
  border-style: solid;
  /*display:flex;*/
  display: grid;
  grid-template-columns: 23% 23% 23% 23%;
  gap: 20px 20px;
  
  
  
  
}

.movieBox {
  border-color: aqua;
  border-style: solid;
  display: flex;
  flex-direction:column;
  margin: 2.5%;
  filter:drop-shadow(.2rem .2rem .5rem var(--almost_dark))
}
.movieBox:hover{
  filter:drop-shadow(.0rem .0rem .2rem var(--green_blue));
}
.movieImg {
  width: 100%;
  height: 100%;
}
.imgCont {
  height: 90%;
  position: relative;
}
.movieText {
  display: flex;
  justify-content: space-between;
  color: #010101;
}
.movieAge {
  position: absolute;
  font-weight: bold;
  align-items: flex-start;
  color: white;
  background-color: #010101;
  top: 88%;
  left: 5%;
}
.TextContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 7px;
  
}
.movieTitle {
  margin-left: 5%;
  font-size:20px;
  color: rgb(139, 140, 141);
}
.star {
  background-color: transparent;
  border: none;
  margin-right: 5%;
}
.btn-danger {
  background-color: transparent;
  border: none;
  margin-right: 5%;
}
.btn-edit {
  background-color: transparent;
  border: none;
  margin-right: 5%;
}

@media (max-width:600px) {
  .movieContainer{
    grid-template-columns:90%;
    justify-content: center;
  }
.movieAge {
  top: 90%;
  left: 3%;
}
.TextContainer{
padding-top: 20px;  
}

}